.App {
  font-family: Arial, sans-serif;
  margin: 20px;
}

h1 {
  text-align: center;
  color: #333;
}

.controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

select {
  padding: 5px;
  font-size: 14px;
}

.incident-list {
  list-style-type: none;
  padding: 0;
}

.incident-item {
  background-color: #f9f9f9;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.incident-item h3 {
  margin: 0;
  font-size: 18px;
}

.incident-item p {
  margin: 5px 0;
  font-size: 14px;
}

button {
  background-color: #007bff;
  color: white;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.incident-form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
}

.incident-form input,
.incident-form textarea,
.incident-form select {
  padding: 10px;
  margin-bottom: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.incident-form button {
  background-color: #28a745;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.incident-form button:hover {
  background-color: #218838;
}
